.preloader {
  display: flex;
  position: fixed;
  z-index: 10000000000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: $color-night-rider;
  transition: visibility linear 0.3s, z-index linear 0.3s, clip-path linear 0.3s;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 110%);
}

.preloader.is-hidden {
  visibility: hidden;
  z-index: -1000;
  pointer-events: none;
  clip-path: polygon(0 0, 100% 0, 100% 0%, 0 0%);
}


.preloader__wrapper {
  transform-style: preserve-3d;
  animation: bouncing $animation-duration infinite;
}

.preloader__cube {
  transform-style: preserve-3d;
  transform: rotateX(45deg) rotateZ(45deg);
  animation: rotation $animation-duration infinite;
}

.preloader__cube-faces {
  transform-style: preserve-3d;
  height: $size;
  width: $size;
  position: relative;
  transform-origin: 0 0;
  transform: translateX(0) translateY(0) translateZ(calc(-80 / 2));
}

.preloader__cube-face {
  position: absolute;
  inset: 0;
  background: $color-default-white;
  border: solid 1px $color-default-black;

  &.shadow {
    transform: translateZ(-$size);
    animation: bouncing-shadow $animation-duration infinite;
  }

  &.top {
    transform: translateZ($size);
  }

  &.front {
    transform-origin: 0 50%;
    transform: rotateY(-90deg);
  }

  &.back {
    transform-origin: 0 50%;
    transform: rotateY(-90deg) translateZ(-$size);
  }

  &.right {
    transform-origin: 50% 0;
    transform: rotateX(-90deg) translateY(-$size);
  }

  &.left {
    transform-origin: 50% 0;
    transform: rotateX(-90deg) translateY(-$size) translateZ($size);
  }
}

@keyframes rotation {
  $start: 45deg;

  0% {
    transform: rotateX($start) rotateY(0) rotateZ($start);
    animation-timing-function: cubic-bezier(0.17, 0.84, 0.44, 1);
  }

  50% {
    transform: rotateX($start) rotateY(0) rotateZ($start + calc(360 / 2));
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
  }

  100% {
    transform: rotateX($start) rotateY(0) rotateZ($start+360);
    animation-timing-function: cubic-bezier(0.17, 0.84, 0.44, 1);
  }
}

@keyframes bouncing {
  0% {
    transform: translateY(-$size*0.5);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
  }

  45% {
    transform: translateY($size*0.5);
    animation-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
  }

  100% {
    transform: translateY(-$size*0.5);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
  }
}

@keyframes bouncing-shadow {
  0% {
    transform: translateZ(-$size) scale(1.3);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
    opacity: 0.05;
  }

  45% {
    transform: translateZ(0);
    animation-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
    opacity: 0.3;
  }

  100% {
    transform: translateZ(-$size) scale(1.3);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
    opacity: 0.05;
  }
}
